import React from 'react';
import styled from 'styled-components';
import {InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import SmallTitle from '../Components/SmallTitle';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import SchoolIcon from '@material-ui/icons/School';
import ResumeItem from '../Components/ResumeItem';

function Resume() {
    const briefcase = <BusinessCenterIcon />
    const school = <SchoolIcon />
    return (
        <ResumeStyled>
            <Title title={'Resume'}  />
            <InnerLayout>
                <div className="small-title">
                    <SmallTitle icon={briefcase} title={'Working Experience'} />
                </div>
                <div className="resume-content">
                    <ResumeItem 
                        year={'2020 - 2021'} 
                        title={'Bug Bounty and ctfs'}
                        subTitle={'Hackerone & Bugcrowd'}
                        text={'Found Some Bugs In Private Programs'} 
                    />
                </div>
                <div className="small-title u-small-title-margin">
                    <SmallTitle icon={school} title={'Educational Qualifications'} />
                </div>
                <div className="resume-content ">
                    <ResumeItem 
                        year={'2017 - 2021'}
                        
                        title={'Computer Science Engineering'}
                        subTitle={'Uttarakhand Technical University'}
                        text={'Percentage Obatained = 65%'} 
                    />
                    <ResumeItem 
                        year={'2015 - 2017'} 
                        title={'Higher Secondary Education'}
                        subTitle={'Radiant Higher Secondary School'}
                        text={'Percentage Obatained = 60%'} 
                    />
                    <ResumeItem 
                        year={'2015 - 2017'} 
                        title={' Secondary Level Eductaion'}
                        subTitle={'Shree New Saraswati Vidya Mandir Airy '}
                        text={'Percentage Obatained = 76.18% '} 
                    />
                </div>
            </InnerLayout>
        </ResumeStyled>    
    )
}

const ResumeStyled = styled.section`
    .small-title{
        padding-bottom: 3rem;
    }
    .u-small-title-margin{
        margin-top: 4rem;
    }

    .resume-content{
        border-left: 2px solid var(--border-color);
    }
`;
export default Resume
