import React from 'react'
import Typewriter from "typewriter-effect";
import styled from 'styled-components';
import FacebookIcon from '@material-ui/icons/Facebook';
import GithubIcon from '@material-ui/icons/GitHub';
import Particle from '../Components/Particle';
import {  Instagram, LinkedIn } from '@material-ui/icons';

function HomePage() {
    return (
        <HomePageStyled>
            <div className="particle-con">
                <Particle />
            </div>
            <div className="typography">
                
                <h1>Hi, I'm <span>Himal Rawal</span></h1>
                <p>
                    Who  am I ? <span><Typewriter
                    options={{autoStart:true,
                    loop:true,
                    delay:40,
                    strings:[
                        " I am hacker",
                        "I am coder",
                        "I am Student",
                        "I am Quick Learner"
                    ]
                            }}
                    /></span> <br></br>
                    I am From Nepal. I am a quick learner. <br></br>
                    I have completed My B.Tech From Uttarakhand Technical University  in 2021.And Currently I am Searching for job
                    until then  I will be contributing to open source and will be doing bug bounty. I am interested in cyber security
                    currently learning web app Penetration Testing. If you want to learn together feel free to ping me 
                    through email or facebook. You can follow me or we can be friends by clicking on the below links. 
                    <br></br><br></br>
                    If you guys are hiring a candidate for posts like Security Analyst, Flutter Developers, React Developers,
                    python related posts,please let me know. Mail me at himalrawal07@gmail.com

                </p>
                <div className="icons">
                    <a href="https://www.facebook.com/himal.rawal.969/" className="icon i-facebook">
                        <FacebookIcon />
                    </a>
                    <a href="https://github.com/Traitor00" className="icon i-github">
                        <GithubIcon />
                    </a>
                    <a href="https://www.instagram.com/himalrawal07/" className="icon i-instagram">
                        <Instagram />
                    </a>
                    <a href="https://www.linkedin.com/in/himal-rawal/" className="icon i-linkedin">
                        <LinkedIn/>
                    </a>
                    
                </div>
            </div>
        </HomePageStyled>
    )
}

const HomePageStyled = styled.header`
    width: 100%;
    height: 100vh;
    position: relative;
   
    .typography{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        width: 80%;

        .icons{
            display: flex;
            justify-content: center;
            margin-top: 1rem;
            .icon{
                border: 2px solid var(--border-color);
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                transition: all .4s ease-in-out;
                cursor: pointer;
                &:hover{
                    border: 2px solid var(--primary-color);
                    color: var(--primary-color);
                }
                &:not(:last-child){
                    margin-right: 1rem;
                }
                svg{
                    margin: .5rem;
                }
            }

            .i-instagram{
                &:hover{
                    border: 2px solid red;
                    color: red;
                }
            }
            .i-github{
                &:hover{
                    border: 2px solid #5F4687;
                    color: #5F4687;
                }
            }
        }
    }
`;

export default HomePage;
